.comparison-1 {

    margin: 0 auto;
    
    @media screen and (min-width: 1100px) {
        margin: unset;
    }

    @include base;

    

    &__content {
        color: #8c8c8c;
        font-size: 2rem;
        flex: 1 1 100%;
        padding-top: 4rem;
        padding-right: 4rem;
        padding-left: 4rem;

            & h1 {
                font-size: 5rem;
                text-align: left;
                color: #fff;

                @media screen and (min-width: 1100px) {
                    font-size: 6rem;
                }
    
            }

            & p {
                font-size: 2rem;
                text-align:left; 
                padding-bottom: 2rem;
                color: #fff; 
            }

            & sup {
                text-align: left;
            }

            & li {
                font-size: 2.4rem;
                font-weight: normal;
                list-style-type: none;
            }

            &__value {
                font-size: 12.9rem;
                font-weight: normal;
            }
    }



    @media screen and (min-width: 1100px) {

        display: flex;

    }

}

.comparison-2 {
    @include base;

    &__content {
        flex: 1 1 50%;
    }

    &__circles {
        padding-top: 4rem;
        text-align: left;
    }

    &__circle {
        margin: 4rem auto;
        width: 250px;
        height: 250px;
        border-radius: 50%;
        text-align: center;
        color: #fff;
        line-height: 250px;
        font-weight: 600;
        position: relative;

        

    @media screen and (min-width: 1000px) {
        // display: inline-block;
        // margin: 0 4rem;
        // flex: 1 1 25%;
    }

    &::before, &::after {
        content: '';
        border: 1.5rem solid #aaa;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        border-color: $yellow #fff #fff $yellow;
        // transform: rotate(-45deg);
        transition: transform 1s;
    }

    &::after {
        border-color: transparent transparent white transparent;
    }

    &::before {
        transform: rotate(135deg);
    }
        &-value {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            font-size: 5rem;
            margin: 0;
            padding: 0;
        }

        &-label {
            position: absolute;
            top: 55px;
            bottom: 0;
            left: 0;
            right: 0;
            font-size: 4rem;
            margin: 0;
            padding: 0;
            font-weight: 400;

            &-a {
                font-size: 2rem;
                position: absolute;
                top: 55px;
                bottom: 0;
                left: -1px;
                right: 0;
            }
        }

        &-percent {
            position: absolute;
            top: 30px;
            bottom: 0;
            left: 0;
            right: 0;
            font-size: 2rem;
            margin: 0;
            padding: 0;
            font-weight: 400;

            &-1 {
                position: absolute;
                top: -30px;
                bottom: 0;
                left: 0;
                right: 0;
                font-size: 6rem;
                margin: 0;
                padding: 0;
                font-weight: 400;
            }
        }
    }


}

