@mixin base {
    min-height: 100vh;
    background-image: radial-gradient(circle at 50% 0, rgba(121, 167, 196, 0.5), rgba(31, 51, 64, 0.5)), linear-gradient(#1f3340, #1f3340);
    // background-image: url('../../../images/empact.jpg');
    // background-repeat: no-repeat;
    // background-size: 100%;

    @media screen and (min-width: 1330px) {
        min-height: 50vh;
    }
}

.agile-projects {
    @include base;

    @import 'agile-projects-1';
}